define('dummy/components/chart-fish-over-time', ['exports', 'ember', './chartist-chart'], function (exports, Ember, ChartistChart) {

  'use strict';

  function getData () {
    return new Ember['default'].RSVP.Promise(function(resolve) {
      Ember['default'].run.later(function() {
        resolve({
          labels: [
            'Salmon', 'Yellowtail', 'Dolphin', 'Cow'
          ],
          series: [
            ['2', '8', '3', '9']
          ]
        });
      }, 500);
    });
  }

  exports['default'] = ChartistChart['default'].extend({
    init: function () {
      getData().then(function (data) {
        this.set('data', data);
      }.bind(this));

      this._super();

      // An example showing that the chart will update when the data changes.
      Ember['default'].run.later(function() {
        var newData = {
          labels: [
            'Salmon', 'Yellowtail', 'Dolphin', 'Cow'
          ],
          series: [
            ['22', '18', '0', '19']
          ]
        };

        this.set('data', newData);
      }.bind(this), 4000);
    },

    ratio: 'ct-minor-seventh',
    options: {
      showPoint: false,
      axisY: {
        offset: 0,
        showLabel: false,
        showGrid: true,
      },
      axisX: {
        showGrid: false,
      }
    },

    responsiveOptions: [
      ['screen and (min-width: 640px)', {
        showPoint: true,
        axisY: {
          offset: 50,
          showLabel: true
        }
      }]
    ]
  });

});